'use client'

export default function LeftMenuBtn({
  children,
  iconTag
}: {
  children: React.ReactNode;
  iconTag: string,
}) {
    return (
      <>
      <button className="block w-10 h-10 cursor-pointer bg-transparent">
        {children}
      </button>
      <span className="text-[14px] text-gray-200 w-30">{iconTag}</span>  
      </>
    )
}